import React from "react";

function Tagline() {
  this.setState = { tagline: "blah" };
  return <p>booooo</p>;
}

Tagline.tagline = () => {
  var items = [
    "it's my only resolution",
    "is not a problem",
    "is my bare minimum",
    "is a reason to wake up in the morning",
    "couldn't be any better",
    "is the jam"
  ];
  var item = items[Math.floor(Math.random() * items.length)];
  return item;
};

export default Tagline;
